<template>
  <v-container>
    <v-carousel
      light
      :continuous="false"
      :hide-delimiters="true"
      :show-arrows="false"
      v-model="carouselIndex"
    >
      <v-carousel-item v-for="n in 5" :key="n">
        <v-card class="ma-2">
          <v-card-title>Tony Stark {{n}}</v-card-title>
          <v-card-text>
            <v-list-item v-for="(scale, index) in scales" :key="index">
              <template v-slot:default="{ active }">
                <v-list-item-action>
                  <v-checkbox :input-value="active"></v-checkbox>
                </v-list-item-action>

                <v-list-item-content>
                  <v-list-item-title>{{scale.name}}</v-list-item-title>
                  <v-list-item-subtitle>{{ scale.description}}</v-list-item-subtitle>
                </v-list-item-content>
              </template>
            </v-list-item>
          </v-card-text>
          <v-card-actions>
            <v-btn text v-if="carouselIndex==0">取消</v-btn>
            <v-btn text @click="previous" v-if="carouselIndex>0">上一個</v-btn>
            <v-btn text right v-if="carouselIndex<4" @click="next" outlined>下一個</v-btn>
            <v-btn right v-if="carouselIndex==4" color="primary">完成</v-btn>
          </v-card-actions>
        </v-card>
      </v-carousel-item>
    </v-carousel>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      scales: [
        { name: "能力指標A", description: "寶寶能力A", checked: false },
        { name: "能力指標B", description: "寶寶能力B", checked: false },
        { name: "能力指標C", description: "寶寶能力C", checked: false },
        { name: "能力指標D", description: "寶寶能力D", checked: false },
        { name: "能力指標E", description: "寶寶能力E", checked: false },
      ],
      carouselIndex: 0,
    };
  },
  methods: {
    previous() {
      this.carouselIndex--;
    },
    next() {
      this.carouselIndex++;
    },
  },
};
</script>

